	module.exports = {"data":{"markdownRemark":{"html":"<p>Chart Search enabled providers to search for medical concepts in the notes left in a patient's medical record.</p>\n<p>Chart Search consisted of several services, notably an indexer that could index related medical concepts that were not necessarily documented the same way.</p>\n<p>I was on the team that inherited Chart Search and maintained it for a few months.</p>\n<p>Of all the solutions I worked on at Cerner, this was the one that had the most users while I was still working on it.</p>","fields":{"project":{"title":"Cerner Chart Search","subtitle":"Semantic search of provider notes in medical records","status":"contracted","createdAtTimestamp":"2011-07-01T00:00:00.000Z","createdAt":"July 2011","deployedUrl":"https://www.cerner.com/solutions/health-systems","endedAtTimestamp":"2013-01-31T00:00:00.000Z","endedAt":"January 2013","sourceUrl":null}}},"site":{"siteMetadata":{"title":"Adam Stegman"}}},"pathContext":{"slug":"/projects/cerner-chart-search"}}

